// App.js
// Entry point of the application; initializes the PIXI application and sets up the world.

import React, { useEffect, useRef } from 'react';
import * as PIXI from 'pixi.js';
import { createPlayer } from './Player';
import { setupWorld } from './World';
import { setupInteraction } from './Interaction';
import { config } from './config';

import './App.css';

function App() {
  const appRef = useRef(null);

  useEffect(() => {
    const app = new PIXI.Application({
      width: window.innerWidth,
      height: window.innerHeight,
      backgroundColor: config.BACKGROUND_COLOR,
      resizeTo: window,
    });

    appRef.current.appendChild(app.view);

    const worldContainer = new PIXI.Container();
    app.stage.addChild(worldContainer);

    const { playerContainer, updateTrail } = createPlayer(app);
    const player = { container: playerContainer, updateTrail };
    

    const caches = setupWorld(app, worldContainer, player);

    setupInteraction(app, worldContainer, player, caches);

    return () => {
      app.destroy(true, true);
    };
  }, []);

  return <div ref={appRef} id="game-container" />;
}

export default App;
