// Player.js
// Manages the player character and its animations.

import * as PIXI from 'pixi.js';
import { parseColorString, createRadialGradientTexture } from './utils';
import { config } from './config';

export function createPlayer(app) {
    //ESTABLISH PLAYER CONTAINER AND CONFIG
    const playerContainer = new PIXI.Container();
    const baseColor1 = parseColorString(config.PLAYER_BASE_COLOR);
    const baseColor2 = parseColorString(config.PLAYER_BASE_COLOR);
    const layerCount = config.PLAYER_LAYER_COUNT;
    const maxSize = config.PLAYER_MAX_SIZE;

    //CREATE LAYERS OF PLAYER ORB FOR GLOW EFFECT
    const layers = createLayers(playerContainer, baseColor1, baseColor2, layerCount, maxSize);

    //CORE
    const core = createLayer(config.TILE_SIZE / 6, 0.4, baseColor1);
    playerContainer.addChild(core);
    layers.push({ sprite: core, offsetX: 0, offsetY: 0, velocity: { x: 0, y: 0 }, });
    playerContainer.pivot.set(maxSize, maxSize);
    playerContainer.x = Math.floor(window.innerWidth / 2 + config.TILE_SIZE / config.PLAYER_SIZE_FACTOR);
    playerContainer.y = Math.floor(window.innerHeight / 2 + config.TILE_SIZE / config.PLAYER_SIZE_FACTOR);

    //PLAYER ANIMATION
    let time = 0;
    const maxOffset = config.TILE_SIZE / 2;
    animatePlayer(layers, config, app, maxOffset, layerCount, time, playerContainer)
    const updateTrail = (dx, dy) => { updateTrailForLayers(dx, dy, layers, layerCount); }


    app.stage.addChild(playerContainer);
    return { playerContainer, updateTrail };
}





function animatePlayer(layers, config, app, maxOffset, layerCount, time, playerContainer) {
    app.ticker.add((delta) => {
        time += config.PLAYER_ANIMATION_SPEED * delta;
        const basePulse = Math.sin(time * 1.2) * 0.1 + 1;
        layers.forEach((layer, index) => {
            const layerPulse = basePulse + Math.sin(time * 0.3) * 0.1;
            layer.sprite.scale.set(layerPulse, layerPulse);

            const springForceX = -config.SPRING_STRENGTH * layer.offsetX;
            const springForceY = -config.SPRING_STRENGTH * layer.offsetY;

            layer.velocity.x += springForceX;
            layer.velocity.y += springForceY;

            layer.velocity.x *= config.DAMPING;
            layer.velocity.y *= config.DAMPING;

            layer.offsetX += layer.velocity.x * delta;
            layer.offsetY += layer.velocity.y * delta;

            const offsetLength = Math.sqrt(layer.offsetX ** 2 + layer.offsetY ** 2);
            if (offsetLength > maxOffset) {
                const scale = maxOffset / offsetLength;
                layer.offsetX *= scale;
                layer.offsetY *= scale;
            }

            const reversedIndex = layerCount - index;
            layer.sprite.x = layer.offsetX * (reversedIndex / layerCount);
            layer.sprite.y = layer.offsetY * (reversedIndex / layerCount);
        });

        const bounce = Math.sin(time * config.PLAYER_BOUNCE_FACTOR) * (config.TILE_SIZE / 10);
        playerContainer.y = Math.floor(window.innerHeight / 2) + bounce + config.TILE_SIZE / 3;
    });
}


function createLayers(playerContainer, baseColor1, baseColor2, layerCount, maxSize) {
    const layers = [];
    for (let i = 0; i < layerCount; i++) {
        const size = maxSize - (i * maxSize) / layerCount;
        const alpha = 0.05 + i * 0.05;
        const t = i / (layerCount - 1);
        const layerColor = interpolateColor(baseColor1, baseColor2, t);
        const layer = createLayer(size, alpha, layerColor);
        playerContainer.addChild(layer);
        layers.push({
            sprite: layer,
            offsetX: 0,
            offsetY: 0,
            velocity: { x: 0, y: 0 },
        });
    }
    return layers;
}

function updateTrailForLayers(dx, dy, layers, layerCount) {
    const scrollSpeed = Math.sqrt(dx * dx + dy * dy);
    const velocityScale = Math.min(scrollSpeed / 10, 1) * config.MAX_VELOCITY;

    layers.forEach((layer, index) => {
        const layerFactor = (layerCount - index) / layerCount;
        layer.velocity.x += dx * layerFactor * velocityScale;
        layer.velocity.y += dy * layerFactor * velocityScale;
    });
}



function createLayer(size, alpha, color) {
    const layer = new PIXI.Graphics();
    const texture = createRadialGradientTexture(size, color, alpha);
    const sprite = new PIXI.Sprite(texture);
    sprite.anchor.set(0.5, 0.5);
    sprite.width = sprite.height = size * 2;

    return sprite;
}

function interpolateColor(color1, color2, t) {
    const r1 = (color1 >> 16) & 255,
        g1 = (color1 >> 8) & 255,
        b1 = color1 & 255;
    const r2 = (color2 >> 16) & 255,
        g2 = (color2 >> 8) & 255,
        b2 = color2 & 255;

    const r = Math.round(r1 + (r2 - r1) * t);
    const g = Math.round(g1 + (g2 - g1) * t);
    const b = Math.round(b1 + (b2 - b1) * t);

    return (r << 16) | (g << 8) | b;
}