// Terrain.js
// Contains functions related to terrain generation and chunk creation.

import * as PIXI from 'pixi.js';
import { createTerrainDetail } from './TerrainDetails';
import { generateNoiseValues, determineTerrainType, TERRAIN_INFO } from './TerrainTypes';
import { createTexturedTile } from './TerrainTextures';
import { config } from './config';

function generateTile(chunkX, chunkY, x, y, tileCache, noiseMaps, app, detailContainer, simplex, detailCache, terrainTypeCache) {
  const worldX = chunkX * config.CHUNK_SIZE + x;
  const worldY = chunkY * config.CHUNK_SIZE + y;

  const noiseValues = generateNoiseValues(worldX, worldY, simplex);
  const terrain = determineTerrainType(noiseValues);
  const { light, dark } = TERRAIN_INFO.find((t) => t.type === terrain).colors;

  const baseColor = light ?? 0x000000;
  const secondColor = TERRAIN_INFO.find((t) => t.type === terrain).colors?.secondary ?? undefined;
  const darkColor = dark ?? baseColor;

  const tile = createTexturedTile(terrain, baseColor, secondColor, darkColor, tileCache, noiseMaps, app);
  tile.position.set(x * config.TILE_SIZE, y * config.TILE_SIZE);
  detailContainer.parent.addChildAt(tile, 0);  // Add tile to the main chunk container

  createTerrainDetail(terrain, detailCache, x, y, detailContainer, worldX, worldY);
  // Cache the terrain type
  terrainTypeCache.set(`${worldX},${worldY}`, terrain);
}

export function generateChunk(chunkX, chunkY, tileCache, noiseMaps, app, chunks, simplex, worldContainer, detailCache, terrainTypeCache) {
  const chunkKey = `${chunkX},${chunkY}`;
  if (chunks[chunkKey]) return;

  const chunkContainer = new PIXI.Container();
  chunkContainer.position.set(chunkX * config.CHUNK_SIZE * config.TILE_SIZE, chunkY * config.CHUNK_SIZE * config.TILE_SIZE);

  // Create a separate container for details within the chunk
  const detailContainer = new PIXI.Container();
  chunkContainer.addChild(detailContainer);

  for (let x = 0; x < config.CHUNK_SIZE; x++) {
    for (let y = 0; y < config.CHUNK_SIZE; y++) {
      generateTile(chunkX, chunkY, x, y, tileCache, noiseMaps, app, detailContainer, simplex, detailCache, terrainTypeCache);
    }
  }

  worldContainer.addChild(chunkContainer);
  chunks[chunkKey] = chunkContainer;
}

// New function to sort details within each chunk
function sortChunkDetails(chunk) {
  if (chunk.children.length > 1 && chunk.children[1] instanceof PIXI.Container) {
    const detailContainer = chunk.children[1];
    detailContainer.children.sort((a, b) => a.position.y - b.position.y);
  }
}

// New function to sort all chunks
export function sortAllChunks(chunks) {
  Object.values(chunks).forEach(sortChunkDetails);
}